import React, { Component } from 'react';
import { Menu } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xnavmenu extends Component {

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	onSelect = (n) => {
		console.log(n)
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>NavMenu 导航菜单</h3>
				<div className="mt-20 mb-20">
					<Menu theme="dark" defaultActive="1" className="el-menu-demo" mode="horizontal" onSelect={this.onSelect}>
						<Menu.Item index="1">处理中心</Menu.Item>
						<Menu.SubMenu index="2" title="我的工作台">
							<Menu.Item index="2-1">选项1</Menu.Item>
							<Menu.Item index="2-2">选项2</Menu.Item>
							<Menu.Item index="2-3">选项3</Menu.Item>
						</Menu.SubMenu>
						<Menu.Item index="3">订单管理</Menu.Item>
					</Menu>
				</div>
				<Codemirror value={
					`<Menu theme="dark" defaultActive="1" className="el-menu-demo" mode="horizontal" onSelect={this.onSelect}>
	<Menu.Item index="1">处理中心</Menu.Item>
	<Menu.SubMenu index="2" title="我的工作台">
		<Menu.Item index="2-1">选项1</Menu.Item>
		<Menu.Item index="2-2">选项2</Menu.Item>
		<Menu.Item index="2-3">选项3</Menu.Item>
	</Menu.SubMenu>
	<Menu.Item index="3">订单管理</Menu.Item>
</Menu>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xnavmenu)
